@import url('https://fonts.googleapis.com/css2?family=Rubik:ital,wght@0,300..900;1,300..900&display=swap');

*{
    margin: 0;
    padding: 0;
    font-family: "Rubik", sans-serif;
}

.flex{
    display: flex;
}

.center{
    display: flex;
    justify-content: center;
}

.space-between{
    justify-content: space-between;
}

a{
    color: #ffffff;
    text-decoration: none;
}
.navbar{
    background-color: #b9b9b9;
}

#nav-logo{
    height: 8vh;
    margin-top: 10px;
}

#nav-id-logo{
    height: 5vh;
    margin: 0 1vw;
}

#link-div{
    align-items: center;

}

#link-div div a{
    margin:0 2vw;
    font-size: 2vh;
    color: #000000;
    font-family:Haettenschweiler, 'Arial Narrow Bold', sans-serif;
    text-decoration: none;
}

.logo-name{
    display: flex;
    align-items: center;
}

thead{
    font-weight: 600;
}

/* login css starting */

#login-box2{
    margin:20vh 25vw;
    padding:3rem;
    border: 1px solid black;
    border-radius: 3rem;
    background-color: #f5f5f5;

}

#login-box2 h1{
    font-family: "Rubik", sans-serif;
}

#login-Email, #login-Password{
    width: 30vw;
    border: 1px solid black;
    border-radius: 1rem;
    padding:0.5rem 1rem;
    margin: 2vh 0;
    font-size: 1rem;
}

#login-Button{
    border:1px solid black;
    border-radius: 2rem;
    padding: 0 2rem;
    font-size: 2vh;
    margin:2vh;
}

#Signin-Link{
    margin:0 7vw;
    justify-content: end;
    font-family: "Rubik", sans-serif;
}

#Signin-Link a {
    text-decoration: none;
    color: black;
    justify-content:left;
}
/* login css ending */

/* signin css starting */

#signin-box h1{
    font-family: "Rubik", sans-serif;
    margin: 0 1vh;
}
#signin-box{
    margin:20vh 25vw;
    padding:3rem;
    border: 1px solid black;
    border-radius: 3rem;
    background-color: #f5f5f5;
}

#signin-Email,#signin-Password,#signin-check-Password{
    width: 30vw;
    border: 1px solid black;
    border-radius: 1rem;
    padding:0.5rem 1rem;
    margin: 2vh 0;
    font-size: 1rem;
}

#login-Link{
    margin:0 7vw;
    font-family: "Rubik", sans-serif;
    justify-content: end;
}

#login-Link a{
    text-decoration: none;
    color: black;
    justify-content:left;
}
#signin-Button{
    border:1px solid black;
    border-radius: 2rem;
    padding: 0 2rem;
    font-size: 2vh;
    margin:2vh;
}

@media (max-width:50vh) {
    
}
/* signin css ending */

/* home css starting */

.ver-nav{
    background-color: #12336a;
    width:10vw;
    height:88.6vh;
    padding:1vh 1vw;
}


.main-scroll{
    height: 90.6vh;
    width: 90vw;
    padding: 0 2vw;
    overflow-x:scroll;
    overflow-y:scroll;
    scroll-behavior: smooth;
    background-color:#fafafa;

}

.ver-nav-div{
    margin: 2vh 0;
    font-size: 2vh;
}

.notice-img{
    width: 100%;
    border-radius: 2rem;
}

.notice h1{
    text-align: center;
}

#register{
    font-size: large;
    padding:1vh 1vw;
    background-color: #005eff;
    border-radius: 1rem;
    color:#ffffff;
}
/* home css ending */

/* marks css starting */

#mark-table{
    border-radius: 2rem;
    border-spacing: 0;
    border: 1px solid black;
    width:70vw;
    margin:0 10vw;
    font-size:x-large;
    text-align: center;
    
}
#mark-table td{
    padding:1rem;

}

.mark-table-td{ 
  border-top: 1px solid black;
}

/* marks css ending */

/* courses css starting */

#course-table{
    border-radius: 2rem;
    border: 1px solid black;
    border-spacing: 0;
    width:70vw;
    margin:0 10vw;
    font-size:x-large;
    text-align: center;
    
}

#course-table td{
    padding: 1rem;
}

.course-table-td{
  border-top: 1px solid black;
}

/* courses css ending */

/* fees css starting */

#fees-table{
    border-radius: 2rem;
    border: 1px solid black;
    border-spacing: 0;
    width:70vw;
    margin:0 10vw;
    font-size:x-large;
    text-align: center;

}


.headline h1{
    text-align: center;
}

#fees-table td{
    padding: 1rem;
}

.fees-table-td{
  border-top: 1px solid black;
}

/* fees css ending */